import React from 'react';
import './ImageLinkForm.css';

const ImageLinkForm = ({ onInputChange, onButtonSubmit }) => {
  return (
    <div>
      <p className="description pa3 f3 center">
        {
          'The IntelliBrain will detect faces in you pictures. Try it to see how smart it is!'
        }
      </p>
      <div className="form pa3 w-70 center br3">
        <input
          className="f4 pa1 ma2 w-80 br2 center"
          type="text"
          onChange={onInputChange}
        />
        <button
          className="but w-40 ma2 grow f4 link ph3 pv2 db center black br3 bg-silver b pointer"
          onClick={onButtonSubmit}
        >
          Detect
        </button>
      </div>
    </div>
  );
};

export default ImageLinkForm;
